.Home-container {
    position: relative; 
    padding: 30px; 
    margin-top: 64px;
    right: 0; 
    bottom: 0;
    -webkit-transition: margin-left 450ms cubic-bezier(0.23, 1, 0.32, 1) 0ms;
    -o-transition: margin-left 450ms cubic-bezier(0.23, 1, 0.32, 1) 0ms;
    transition: margin-left 450ms cubic-bezier(0.23, 1, 0.32, 1) 0ms;
    width: 100%;
}


